.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}


.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}



.paper{
  margin: 2rem 0 0 0;
  padding: 1rem 2rem 2rem 1rem;
  display: flex;
  flex-direction: row;
  align-items: center;
  flex-wrap: nowrap;
  justify-content: flex-start;
  align-content: flex-start;
}

.avatar{
  width: 25%;
  object-fit: cover;
  /* /* max-height:30%; */
}

.line-pattern{ 
  display: flex;
  flex-direction: column;
  align-content: center;
  justify-content: space-between;
}

.email{
  display: flex;
  flex-direction: row;
  align-content: center;
}
.email-img{ 
  height:2rem;
}

.space{
  margin: 0 5px;
}

.padding2{
  padding:2rem
}

.padding1{
  padding:1rem
}

.last_margin{
  margin-bottom: 4rem;
}